:root {
  --font-poppins: "Poppins", sans-serif;
}

:root {
  --red: hsl(0, 78%, 62%);
  --cyan: hsl(180, 62%, 55%);
  --orange: hsl(34, 97%, 64%);
  --blue: hsl(212, 86%, 64%);
  --grey-500: hsl(234, 12%, 34%);
  --grey-400: hsl(212, 6%, 44%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: var(--font-poppins);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

img {
  display: block;
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0.625rem;
  line-height: 1.2;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  grid-template-areas: "b" "a" "d" "c";
  justify-items: center;
  gap: 2rem;
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(auto-fit, 1fr);
    grid-template-rows: repeat(4, 0.5fr);
    grid-template-areas: ". a ." "b a c" "b d c" ". d .";
  }
}

.card {
  background-color: var(--white);
  width: 21.875rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  padding: 3.125rem 1.875rem;
  border-radius: 0.3125rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 10px 27px -10px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
@media (min-width: 71.875em) {
  .card {
    width: 20.625rem;
  }
}
.card__text {
  text-align: left;
  margin-bottom: 1.75rem;
}
.card__title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--grey-500);
}
.card__description {
  font-size: 0.9375rem;
  font-weight: 400;
  color: var(--grey-400);
}
.card--supervisor {
  border-top: 0.3125rem solid var(--red);
}
.card--team {
  border-top: 0.3125rem solid var(--cyan);
}
.card--karma {
  border-top: 0.3125rem solid var(--orange);
}
.card--calculator {
  border-top: 0.3125rem solid var(--blue);
}
.card__icon {
  align-self: flex-end;
}
.item-a {
  grid-area: a;
}

.item-b {
  grid-area: b;
  justify-self: end;
}

.item-c {
  grid-area: c;
  justify-self: start;
}

.item-d {
  grid-area: d;
}

.header {
  max-width: 31.25rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  .header {
    margin-top: 1.25rem;
  }
}
.header__title {
  margin-top: 3.125rem;
  font-size: 1.5rem;
  font-weight: 200;
  color: var(--grey-500);
}
@media (min-width: 40em) {
  .header__title {
    font-size: 1.75rem;
  }
}
@media (min-width: 71.875em) {
  .header__title {
    font-size: 2rem;
    margin-top: 0;
  }
}
.header__subtitle {
  font-size: 0.9375rem;
  font-weight: 400;
  color: var(--grey-400);
}
.header .bold {
  font-weight: 600;
  display: block;
}